import {HotelhomeComponent} from './hotelhome/hotelhome.component';
import {RegisterComponent} from './register/register.component';
import {HotelcontactComponent} from './hotelcontact/hotelcontact.component';
import {HotelroomsComponent} from './hotelrooms/hotelrooms.component';
import {HotelFoodComponent} from './hotel-food/hotel-food.component';
import {HotelmenuComponent} from './hotelmenu/hotelmenu.component';
import {HotelsuperiorComponent} from './hotelsuperior/hotelsuperior.component';
import { HoteljuniorComponent } from './hoteljunior/hoteljunior.component';
import { HotelstandardComponent } from './hotelstandard/hotelstandard.component';
import { HotelaboutComponent } from './hotelabout/hotelabout.component';
import {Routes} from '@angular/router';

export const Main_routes : Routes =[
    {path : '' , redirectTo: '/register', pathMatch: 'full'},
    {path : 'homepage' , component:HotelhomeComponent},
    {path : 'rooms' , component:HotelroomsComponent},
    {path : 'contact' , component:HotelcontactComponent},
    {path : 'food' , component: HotelFoodComponent},
    {path : 'menu' , component: HotelmenuComponent},
    {path : 'superior' , component : HotelsuperiorComponent},
    {path : 'junior' ,component: HoteljuniorComponent},
    {path : 'standard' ,component: HotelstandardComponent},
    {path : 'about' ,component: HotelaboutComponent},
    {path : '**' , component:RegisterComponent}
    
 
] 