import { Component, OnInit } from '@angular/core';
import { Food } from '../food';
import { UserFoodService } from '../user-food.service';

@Component({
  selector: 'app-hotel-food',
  templateUrl: './hotel-food.component.html',
  styleUrls: ['./hotel-food.component.css']
})
export class HotelFoodComponent implements OnInit {

  food : Food=new Food("","",0);
  message: any;

  constructor(private service : UserFoodService) { }

  ngOnInit() {
  }

  getUrl()
  {
    return "url('../../assets/images/eat5.jpg')";
  }

  getUrl1()
  {
    return "url('../../assets/images/eat9.jpg')";
  }

  getUrl2()
  {
    return "url('../../assets/images/eat2.jpg')";
  }

  public booktable()
  {
    let response = this.service.doReservation(this.food);
    response.subscribe((data) => this.message = data);
  }

 

}
