import { Component, OnInit } from '@angular/core';
import { Room } from '../room';
import { UserRoomService } from '../user-room.service';

@Component({
  selector: 'app-hotelrooms',
  templateUrl: './hotelrooms.component.html',
  styleUrls: ['./hotelrooms.component.css']
})
export class HotelroomsComponent implements OnInit {

  room : Room =new Room("","",0,0);
  message : any;


  
  constructor(private service: UserRoomService) { }

  public roomready()
  {
    let response = this.service.doReservationRoom(this.room);
    response.subscribe((data) => this.message = data);
  }

  ngOnInit() {
  }

  getUrl()
  {
    return "url('../../assets/images/room1.jpg')";
  }
}
