import { Component, OnInit } from '@angular/core';
import {UserRegistrationService} from './../user-register.service'
import {User} from './../user'
import {Loger} from './../loger'
import { Router } from '@angular/router';


@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  user: User = new User("","","","","","");
  user1 : Loger = new Loger("","");
  message: any;
  credentials : any;
  flag : number =0;

  page={
    title : 'Register Yourself',
    image : '2.jpg'
  };
  service1: any;

  constructor(private router : Router , private service: UserRegistrationService) { }

  ngOnInit() {
    document.querySelector('.img__btn').addEventListener('click', function(){
      document.querySelector('.cont').classList.toggle('s--signup');
    });
  }

  
  public registerProcess() {
    let response = this.service.doRegistration(this.user);
    response.subscribe((data) => this.message = data);
    }

    public doLogin()
    {
      let response = this.service.gettingCredentials();
      response.subscribe((data) => this.credentials = data);

      for(let i in this.credentials)
      {
        if(this.user1.email== this.credentials[i].email && this.user1.password == this.credentials[i].password)
        {
          this.router.navigateByUrl('/homepage');
          this.flag=1;
          break;
        }
      }

      if(this.flag==0)
      {
        this.message="*Invalid credentials";
      }
    }


}
