import { Component, OnInit } from '@angular/core';
import { Contact } from '../contact';
import { UserContactService } from '../user-contact.service';

@Component({
  selector: 'app-hotelcontact',
  templateUrl: './hotelcontact.component.html',
  styleUrls: ['./hotelcontact.component.css']
})
export class HotelcontactComponent implements OnInit {

  contact : Contact=new Contact("","","");
  message: any;

  constructor(private service : UserContactService) { }

  ngOnInit() {
  }

  getUrl1()
  {
    return "url('../../assets/images/contact.jpg')";
  }


  public cont()
  {
    let response = this.service.doContact(this.contact);
    response.subscribe((data) => this.message = data);
  }


}
